<template>
  <div id="FilePreview">
    <van-image v-if="supportValue === 'picture'" :fit="fit" :src="imgUrl" @click="preview"
      :style="{ width: width, height: height }">
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
      <template v-slot:error>{{ errMsg }}</template>
    </van-image>

    <div v-else-if="fileExt === 'txt'" style="font-size: 1.5rem; line-height: 1.5; padding: 0 2vw;">
      <p>{{ txtValue }}</p>
    </div>
    <div v-else-if="supportValue === 'office'" id="demo"></div>
    <div v-else-if="supportValue === 'media'">
      <t-video :src="mediaSrc" :poster="mediaPoster"></t-video>
    </div>
    <div v-else>{{ msg }}</div>
  </div>
</template>
<script>
/****
 * 
 * gfy
 * pdfh5.jssvgcanvas
 * 
 *          if (options.renderType === "svg") {
 *            options.scale= 1; // 
 *             // return self.renderSvg(page, scaledViewport, pageNum, num, container, options, viewport)
 *           }
 *           return self.renderCanvas(page, scaledViewport, pageNum, num, container, options)
 * 2019-11-12
 *  */
import Pdfh5 from "pdfh5";
import tVideo from "@/components/r-video";
import { ImagePreview } from "vant";
import defaultBg from "@/assets/video/video.png";
export default {
  name: "r-file-preview",
  components: { tVideo },
  props: {
    fileExt: {
      type: String,
      default: ""
    },
    fileId: {
      type: String,
      default: ""
    },
    // maxWidth: {
    //   type: String,
    //   default: "10rem"
    // },
    // maxHeight: {
    //   type: String,
    //   default: "10rem"
    // },
    width: {
      type: String,
      default: "100vw"
    },
    height: {
      type: String,
      default: "80vh"
    },
    fit: {
      type: String,
      default: "contain"
    },

    errMsg: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      supportValue: "",
      supportFileExt: [
        {
          type: "office",
          exts: [
            "txt",
            "pdf",
            "doc",
            "docx",
            "xls",
            "csv",
            "xlsx",
            "ppt",
            "pptx"
          ]
        },
        { type: "picture", exts: ["bmp", "jpg", "jpeg", "png", "gif", "svg"] },
        { type: "media", exts: ["mp4", "ogg", "ogv", "webm"] }
      ],
      imgUrl: "",
      imagePreview: null,
      txtValue: "",
      mediaSrc: "",
      mediaPoster: defaultBg,
      msg: "",
      pdfh5: null
    };
  },
  mounted() {
    this.$nextTick(() => {
      let item = this.supportFileExt.find(
        r => r.exts.indexOf(this.fileExt) > -1
      );
      if (item) {
        this.supportValue = item.type;
        switch (item.type.toLowerCase()) {
          case "office":
            this.getPDF();
            break;
          case "picture":
            this.imgUrl = this.$mainHttp.resourse.file(this.fileId);
            break;
          case "media":
            this.mediaSrc = this.$mainHttp.resourse.file(this.fileId);
            break;
        }
      } else {
        this.msg = "";
      }
    });
  },
  methods: {
    preview() {
      this.imagePreview = ImagePreview({
        images: [this.imgUrl],
        showIndex: false,
        onClose() {}
      });
    },
    getPDF() {
      this.$mainHttp
        .post("/FJRH.Campus/PreviewFileByMID", { mid: this.fileId })
        .then(res => {
          if (res.Result) {
            if (this.fileExt === "txt") {
              this.txtValue = res.Data;
            } else {
              this.pdfh5 = new Pdfh5("#demo", {
                data: res.Data,
                scale: 3
              });
            }
          }
        });
    }
  },
  beforeDestroy() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>
<style lang="less" scoped>
@import "pdfh5/css/pdfh5.css";
/deep/.pinch-zoom-container {
  height: auto !important;
}
/deep/img {
  max-width: 100vw;
}
</style>



